import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { AppComponent } from './app.component';
import { HelloWorldComponent } from './hello-world/hello-world.component';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { HeroBannerComponent } from './hero-banner/hero-banner.component';
import { WhyBleComponent } from './why-ble/why-ble.component';
import { BleDownloadsComponent } from './ble-downloads/ble-downloads.component';
import { BleCommunityComponent } from './ble-community/ble-community.component';
import { PageLandingComponent } from './page-landing/page-landing.component';
import { PageLegalComponent } from './page-legal/page-legal.component';
import { PageMetronomesComponent } from './page-metronomes/page-metronomes.component';

const appRoutes: Routes = [
  { path: '', component: PageLandingComponent },
  { path: 'metronomes', component: PageMetronomesComponent },
  { path: 'legal', component: PageLegalComponent } /*,
  { path: '**', component: PageNotFoundComponent } */
];


@NgModule({
  declarations: [
    AppComponent,
    HelloWorldComponent,
    HeaderComponent,
    FooterComponent,
    HeroBannerComponent,
    WhyBleComponent,
    BleDownloadsComponent,
    BleCommunityComponent,
    PageLandingComponent,
    PageLegalComponent,
    PageMetronomesComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(
      appRoutes,
      { enableTracing: true, useHash: true } // <-- debugging purposes only
    )
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



